<template>
  <div class="addBlog">
    <header>
      <h2>添加博客</h2>
    </header>
    <form v-if="!submited">

      <section>
        <label for="blogTitle">博客标题：
          <input type="text" id="blogTitle" v-model="blog.title">
        </label>
      </section>

      <section>
        <label for="blogContent">博客内容：
          <textarea type="text" id="blogContent" v-model="blog.content"></textarea>
        </label>
      </section>

      <section>
        <h4>博客分类：</h4>
        <label v-bind:for="category" v-for="category in categorys" :key="category">
          <input type="checkbox" v-bind:value="category" v-bind:id="category" v-model="blog.categorys">{{ category }}
        </label>
      </section>

      <section>
        <label>博客作者：
          <select v-model="blog.author">
            <option v-for="author in authors" :key="author">{{ author }}</option>
          </select>
        </label>
      </section>

      <button v-on:click.prevent="submitBlog">提交博客</button>
    </form>
    <div>
      <h3 v-if="submited">您的博客发送成功！</h3>
    </div>

    <div class="show">
      <h2>博客总览</h2>
      <p>博客标题：{{ blog.title }}</p>
      <p>博客内容：{{ blog.content }}</p>
      <p>博客分类：{{ blog.categorys}}</p>
      <p>博客作者：{{ blog.author}}</p>
    </div>
  </div>
</template>

<script>
  import axios from 'axios'
  export default {
    name: "addBlog",
    data() {
      return {
        blog: {
          title: '',
          content: '',
          categorys: [],
          author: '',
          id: ''
        },
        submited: false,
        categorys: ['node.js', 'Vue.js', 'angur.js', 'react.js'],
        authors: ['Benny','Herry','Honey']
      }
    },
    methods: {
      submitBlog: function () {
        // this.$http.post('http://jsonplaceholder.typicode.com/posts', {
        axios.post('/api/posts', {
          title: this.blog.title,
          body: this.blog.content
          // // id: this.blog.id
        })
          .then((data) => {
            this.submited = true
          })
      }
    },
    created() {
      var date = new Date().getTime()
      this.blog.id = date + Math.ceil(Math.random()*100)
    }
  }
</script>

<style scoped>
  .addBlog {
    padding: 10px 20px;
  }
  .addBlog * {
    box-sizing: border-box;
  }
  form section {
    margin: 10px 0;
  }
  form input[type = 'text'], textarea, select {
    display: block;
    padding: 4px 0;
    width: 100%;
    max-width: 1260px;
    height: 26px;
    line-height: 26px;
    font-size: 16px;
  }
  /*form input[type = 'checkbox'] {*/
    /*padding: 0 6px;*/
  /*}*/
  textarea {
    height: 80px;
  }
  h4 {
    margin: 10px 0;
  }
  form button {
    padding: 10px 8px;
    background-color: crimson;
    color: #fff;
    border-radius: 3px;
    border: none;
  }
  form button:active {
    color: crimson;
    background-color: #fff;
  }
  .show {
    width: 100%;
    background-color: antiquewhite;
    padding: 10px;
    margin-top: 10px;
  }
</style>



