<template>
  <div class="showBlogs">
    <blogTitle></blogTitle>
    <h2>博客总览</h2>
    <input type="text" placeholder="search blogs" v-model="search" v-on:enter='serachBlog' >
    <article v-for="blog in serachBlog" :key="blog.id"  v-rainbow="">
      <router-link v-bind:to="'/singleBlog/' + blog.id">
        <h3>{{ blog.id }}. {{ blog.title | to-uppercase }}</h3>
      </router-link>
        <p> {{ blog.body | little }}</p>
    </article>
  </div>
</template>

<script>
  import blogTitle from './blogTitle'

  export default {
    name: "showBlogs",
    data() {
      return {
        blogs: [],
        search: ''
      }
    },
    created() {
      // alert('挂载前')
    //  组件实例化之后拿到数据
    //  1. 拿到数据 resource
    //  2. 渲染界面
      this.$http.get('/Vue-blogs/static/posts1.json')
        .then(data => {
          return data.body
        }, (data) => {
          console.log('数据加载失败！请稍后重试！')
        })
        .then(data => {
          this.blogs = data.slice(0, 10)
          // console.log(this.blogs)
        })
    },
    components: {
      blogTitle: blogTitle
    },
    computed: {
      serachBlog() {
        // 找到多少返回遍历多少
        return this.blogs.filter((blog) => {
          return blog.title.match(this.search)
        })
      }
    },
    filters: {
      // 自定义过滤器 filter
      'to-uppercase': function (value) {
        return value.toUpperCase()
      },
      'little': function (value) {
        return value.slice(0, 100) + '...'
      }
    },
    directives: {
      // 自定义指令
      'rainbow': {
        bind(el, binding, vnode) {
          el.style.backgroundColor = '#' + Math.random().toString(16).slice(2, 8)
        }
      }
    }
  }
</script>

<style scoped>
  .showBlogs *{
    box-sizing: border-box;
  }
  h2 {
    margin: 20px 8px;
  }
  input {
    box-sizing: border-box;
    width: 96%;
    display: block;
    margin: 0 2%;
    max-width: 1260px;
    font-size: 16px;
    padding: 8px 6px;
  }
  article {
    width: 96%;
    margin: 20px 2%;
    background-color: #eee5c9;
    padding: 10px;
    border-radius: 5px;
  }
</style>



