<template>
  <div class="singBlog">
    <blogTitle></blogTitle>
    <article>
      <h3>{{ blog.id }}. {{ blog.title }}</h3>
      <p> {{ blog.body }}</p>
    </article>
  </div>
</template>

<script>
  import blogTitle from './blogTitle'
  export default {
    name: "singleBlog",
    data() {
      return {
        blog: {},
        id: this.$route.params.id
      }
    },
    components: {
      blogTitle
    },
    // 获取数据
    created() {
      this.$http.get('http://jsonplaceholder.typicode.com/posts/' + this.id)
        .then(data => {
        return data.body
      })
        .then(data => {
          return this.blog = data
        })
    }
  }
</script>

<style scoped>
  article {
    width: 96%;
    margin: 20px 2%;
    background-color: #eee5c9;
    padding: 10px;
    border-radius: 5px;
  }
</style>



